export default {
    name: 'KurseListe',

    data() {
        return {
            data: [],
        }
    },
    async fetch() {
        const { json: data } = await this.$kirby.find({
            "query": "page('kurse').children",
            "select": {
                "title": true,
                "introtext": true,
            }
        }, this.$nuxt.context.app.i18n.locale)
        this.data = data
    },
}
