export default {
    name: 'Courses',

    data() {
        return {
            courses: [],
            stundenplan: [] 
        }
    },
    async fetch() {
        const { json: data } = await this.$kirby.find({
            "query": "page('stundenplan').children",
            "select": {
                "title": true,
                "starttime": true,
                "endtime": true,
                "instructor": true,
                "day": true,
            }
        }, this.$nuxt.context.app.i18n.locale)
        this.courses = data

        const { json: sttitle } = await this.$kirby.find({
            "query": "page('stundenplan')",
            "select": {
                "title": true,
            }
        }, this.$nuxt.context.app.i18n.locale)
        this.stundenplan = sttitle
    },

    computed: {
        days(){
            var output = {
                Montag:[],
                Dienstag:[],
                Mittwoch:[],
                Donnerstag:[],
                Freitag:[],
            }
            for (var i=0; i<this.courses.length; i++) {
                output[this.courses[i]["day"]].push(this.courses[i])
            }
            return output
        }
    },
}
